/*
 *  Custom Availability Facet
 *    - Extends the ajax collection facet class to add some additional
 *    functionality.
 *    - Allows for three options: all products, only available products and only
 *    sold out products.
 *    - Changes facet printing to match design layout
 *
 *  Version:
 *    1.0.0 - 2021/11/23
 */

import {
  Facet, FACET_CHECKBOX_SELECTOR, FACET_CHECKBOX_OPTION
} from '@process-creative/slate-ajax-collection';
import { handlize } from '../../../tools/utilities/string-tools';

const CustomDecorateCheckboxFacet = (FacetType) => {
  return class extends FacetType {
    constructor(params) {
      super(params);

      this.allowMultiple = typeof params.allowMultiple === typeof undefined ? true : params.allowMultiple;

      this.container.on('change', FACET_CHECKBOX_SELECTOR, e => this.onChange(e));
    }

    // Is Checked for checkboxes, not in sync with the filter due to DOM delays
    isChecked(option) {
      if(!this.getCheckboxElement(option).prop('checked')) return false;
      return true;
    }

    getCheckedOptions() {
      return this.getOptions().filter(o => this.isChecked(o));
    }

    getCheckboxElement(option) {
      return this.container.find(`${FACET_CHECKBOX_SELECTOR}[${FACET_CHECKBOX_OPTION}="${option}"]`);
    }

    getPrint() {
      let options = this.getOptionsToDraw();
      let x = `<ul class="c-collection-faceted-nav__check-group">`;
      options.forEach((option,i) => {

        const optionHandle = handlize(this.template.escape(option));
        x += `<li class="c-collection-faceted-nav__check-item">
          <div class="o-checkbox">
            <input
              type="checkbox" class="o-checkbox__checkbox u-visually-hidden"
              id="checkbox-${optionHandle}"
              data-index="${i}" data-option="${optionHandle}"
              data-facet-checkbox ${this.isSelected(optionHandle)?'checked':''}
            />
            <label
              class="o-checkbox__label o-paragraph--2"
              for="checkbox-${optionHandle}"
            >${this.getOptionName(option)}</label>
          </div>
        </li>`;
      });
      x += `</ul>`
      return x;
    }

    onChange(e) {
      let option = $(e.target).attr(FACET_CHECKBOX_OPTION);

      if(this.isSelected(option)) return false;

      if(this.allowMultiple) {
        this.setOption(option, this.isChecked(option));
      } else {
        this.setOptions(option);
      }
    }
  }
}

const CustomAvailabilityFacet = class extends Facet {
  getOptions() {
    return ['All', 'Available', 'Sold out'];
  }

  getOptionName(option) {
    const optionHandle = handlize(option);

    if(optionHandle == 'all') {
      return 'All Products';
    } else if(optionHandle == 'available') {
      return 'Only Available';
    }

    return option;
  }

  getTemplate(title, body) {
    return `
      ${this.params.showTitle === false ?
        '' :
        `<div
          class="c-collection-faceted-nav__title o-heading--6"
          data-collection-accordion-title
        >
          ${title}
        </div>`
      }
      <div class="c-collection-faceted-nav__check-body" data-collection-accordion-content>
        ${body}
      </div>
    `;
  }
}

export const CustomAvailabilityCheckboxFacet = CustomDecorateCheckboxFacet(CustomAvailabilityFacet);
