import { SmartTagButtonFacet } from '@process-creative/slate-ajax-collection';
import { t } from '@process-creative/slate-theme-tools';
import { handlize } from '../../../tools/utilities/string-tools';

export class CustomColorTagFacet extends SmartTagButtonFacet {
  getPrint() {
    let options = this.getOptionsToDraw();
    let print = `
      <div class="c-collection-faceted-nav__check-group">
    `;
    print += options.reduce((x,option) => {
      const optionHandle = handlize(this.template.escape(option));
      let isSelected = this.isSelected(optionHandle);

      const colorSwatch = window.Swatches.find(swatch => swatch.handles.includes(optionHandle));
      if(!colorSwatch) return x += '';

      const color = colorSwatch.color;

      if(!color) return x += '';

      return x += `
        <button
          class="c-collection-faceted-nav__btn is-${optionHandle}
          ${isSelected?'is-selected':''}"
          data-button-facet data-option="${optionHandle}"
        >
          ${isSelected? `
            <svg class="c-collection-faceted-nav__btn-svg-outline" fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 46 44"><path d="M32.501 3.501c-8.8-4-18.001-3.345-25.5 3-13 11-2.5 32.5 9 35.5s24.05-3.121 27-12.5c3.499-11.121.5-21-10.5-26Z" stroke="#393939" stroke-width="2"/></svg>
          ` : ''}
          <svg class="c-collection-faceted-nav__btn-svg-fill" fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 41 40"><path d="M29.898 1.559C14.78-3.862 2.17 5.772.319 16.382-.914 26.31 1.176 31.648 11 36.712c17.255 8.895 25.882-2.118 28.757-11.012 4.157-12.857-2.875-21.637-9.86-24.141Z" fill="${color}" /></svg>
        </button>
      `;
    },'');
    print += `</div>`;

    return print;
  }

  getTemplate(title, body) {
    return `
      ${this.params.showTitle === false ?
        '' :
        `<div
          class="c-collection-faceted-nav__title o-heading--6"
          data-collection-accordion-title
        >
          ${title}
        </div>`
      }
      <div class="c-collection-faceted-nav__check-body" data-collection-accordion-content>
        ${body}
        <button data-collection-accordion-reset class="c-collection-faceted-nav__reset is-color o-paragraph--2">
          <span class="c-collection-faceted-nav__reset-inner">
            ${t('collections.collection.reset')}
          </span>
        </button>
      </div>
    `;
  }
}