/*
 *  Collection Template
 *    Contains all the scripts used for the collection template.
 *
 *  Version:
 *    1.0.0 - 2021/11/18
 */

import $ from 'jquery';
import { t } from '../../tools/utilities/string-tools';
import { short, medium } from '../../settings/animation';
import { responsiveMediaAbove } from '../../settings/responsive';
import { CLASS_CLOSED, CLASS_HIDDEN, CLASS_OPEN, CLASS_VISIBLE } from '../../tools/constants/classes';
import {
  AjaxCollectionTemplate, TagFilter, TypeFilter, OPERATION_OR
} from '@process-creative/slate-ajax-collection';
import { CustomLoadMore } from './pagination/CustomLoadMore';
import { CustomSort } from './sort/CustomSort';
import { CustomAvailabilityFilter } from './filters/CustomAvailabilityFilter';
import { CustomAvailabilityCheckboxFacet } from './facets/CustomAvailabilityCheckboxFacet';
import { CustomColorTagFacet } from './facets/CustomColorTagFacet';
import { CustomTypeCheckboxFacet } from './facets/CustomTypeCheckboxFacet';
import { CustomTagCheckboxFacet } from './facets/CustomTagCheckboxFacet';
import { initialiseThumbnails } from '../../objects/product/ProductThumbnail';
import { productThumbnailPrint } from '../../objects/product/product-thumbnail-print';
import { documentDisableScroll, documentEnableScroll } from '../../tools/layout/document';

// Attributes
const ATTR_CONTENT_BLOCK_POSITION = 'data-content-block-position';

// Selectors
const SELECTOR_COLLECTION_TEMPLATE = '[data-collection-template]';

const SELECTOR_FACETED_NAV = '[data-faceted-nav]';
const SELECTOR_FACETED_NAV_GROUP = '[data-faceted-nav-group]';
const SELECTOR_FACETED_NAV_AVAILABILITY = '[data-faceted-nav-availability]';
const SELECTOR_FACETED_NAV_PRODUCT = '[data-faceted-nav-type]';
const SELECTOR_FACETED_NAV_SHAPE = '[data-faceted-nav-shape]';
const SELECTOR_FACETED_NAV_STYLE = '[data-faceted-nav-style]';
const SELECTOR_FACETED_NAV_COLOR = '[data-faceted-nav-color]';

const SELECTOR_ACCORDION_SLIDE = '[data-collection-accordion-slide]';
const SELECTOR_ACCORDION_TITLE = '[data-collection-accordion-title]';
const SELECTOR_ACCORDION_CONTENT = '[data-collection-accordion-content]';

const SELECTOR_NO_RESULTS = '[data-collection-no-results]';
const SELECTOR_LOAD_MORE_BUTTON = '[data-collection-template-load-more]';
const SELECTOR_DRAWER_TRIGGER = '[data-filter-toggle]';
const SELECTOR_FILTERS = '[data-filters]';
const SELECTOR_CUSTOM_SORT = '[data-sort-custom]';
const SELECTOR_PRODUCTS_COUNT = '[data-collection-count-selector]';

const SELECTOR_READ_MORE_CONTAINER = '[data-read-more-container]';
const SELECTOR_READ_MORE_TOGGLE = '[data-read-more-toggle]';

const SELECTOR_CLEAR_FILTERS = '[data-collection-accordion-reset]';
const ATTR_FACETED_NAV_TAG_PREFIX = 'data-faceted-nav-facet-prefix';

class CollectionTemplate extends AjaxCollectionTemplate {
  constructor(container) {
    super(container);
    this.container = container;
    this.setupCollection();
    this.init();
  }

  setupCollection() {
    this.setupFiltersAndFacets();
    this.setElements();
    this.setupEvents();

    // Custom content positioning
    this.pagination.paginateContentAware = true;
  }

  setupFiltersAndFacets() {
    this.draw.setDisplayMode('FirstAvailableVariant');

    // Setup Filters
    [
      this.availabilityFilter = new CustomAvailabilityFilter(this, 'availability'),
      this.typeFilter = new TypeFilter(this, 'type'),
      this.shapeFilter = new TagFilter(this, 'shape', OPERATION_OR),
      this.styleFilter = new TagFilter(this, 'style', OPERATION_OR),
      this.colorFilter = new TagFilter(this, 'color', OPERATION_OR)
    ].forEach(filter => this.filters.addFilter(filter));

    // Setup Facets
    [
      new CustomAvailabilityCheckboxFacet({
        template: this, filter: this.availabilityFilter,
        title: t('collections.collection.filter_availability'),
        container: SELECTOR_FACETED_NAV_AVAILABILITY,
        allowMultiple: false
      }),
      new CustomTypeCheckboxFacet({
        template: this,
        title: t('collections.collection.filter_product'),
        filter: this.typeFilter,
        container: SELECTOR_FACETED_NAV_PRODUCT
      }),
      new CustomTagCheckboxFacet({
        template: this,
        filter: this.shapeFilter,
        title: t('collections.collection.filter_shape'),
        container:  SELECTOR_FACETED_NAV_SHAPE,
        prefixes: ['shape'],
        token: '-'
      }),
      new CustomTagCheckboxFacet({
        template: this,
        filter: this.styleFilter,
        title: t('collections.collection.filter_style'),
        container:  SELECTOR_FACETED_NAV_STYLE,
        prefixes: ['style'],
        token: '-'
      }),
      new CustomColorTagFacet({
        template: this, filter: this.colorFilter,
        title: t('collections.collection.filter_colour'),
        container: SELECTOR_FACETED_NAV_COLOR,
        prefixes: ['color'],
        token: '-'
      })
    ].forEach(facet => this.filters.addFacet(facet));
  }

  setElements() {
    // Elements
    this.count = this.container.find(SELECTOR_PRODUCTS_COUNT);
    this.noResults = this.container.find(SELECTOR_NO_RESULTS);
    this.facets = this.container.find(SELECTOR_FACETED_NAV);
    this.facetToggle = [this.facets, $('body')];
    this.facetGroups = this.container.find(SELECTOR_FACETED_NAV_GROUP);

    // Sort
    this.sort.addSorter(new CustomSort(this, this.container.find(SELECTOR_CUSTOM_SORT)));

    this.sort.sortMethods.BestSelling.name = 'Best Selling';
    this.sort.sortMethods.TitleAscending.name = 'Alphabetically, A - Z';
    this.sort.sortMethods.TitleDescending.name = 'Alphabetically, Z - A';
    this.sort.sortMethods.CreatedAscending.name = 'Oldest to Newest';
    this.sort.sortMethods.CreatedDescending.name = 'Newest to Oldest';

    // Filters
    this.filtersContainer = this.container.find(SELECTOR_FILTERS);

    // Pagination
    this.pagination.setPaginator(new CustomLoadMore(this, this.container.find(SELECTOR_LOAD_MORE_BUTTON)));
  }

  setupEvents() {
    this.container.on('click', SELECTOR_READ_MORE_TOGGLE, e => this.onReadMoreToggle(e))
    this.container.on('click', SELECTOR_DRAWER_TRIGGER, e => this.onFilterDrawerToggle(e));
    this.container.on('click', SELECTOR_CLEAR_FILTERS, e => this.onClearFilterClick(e));
    document.addEventListener('click', e => this.onClickOutsideFacet(e));
    this.initAccordion();

    // Custom content positioning
    this.pagination.paginateContentAware = true;
    this.printedContentBlocks = [];
  }

  initAccordion() {
    $(document).on('click', SELECTOR_ACCORDION_TITLE, e => {
      const target = $(e.target);
      const slide = target.parents(SELECTOR_ACCORDION_SLIDE);
      const content = slide.find(SELECTOR_ACCORDION_CONTENT);

      // If slides closed, close all open slides and open this slide
      if(slide.hasClass(CLASS_CLOSED)) {
        this.container.find(SELECTOR_ACCORDION_SLIDE).each((i, e) => {
          const element = $(e);

          if(!element.hasClass(CLASS_CLOSED)) {
            // On desktop colour slides will fade instead of slide
            if(responsiveMediaAbove('SMALL') && element.hasClass('is-color')) {
              element.find(SELECTOR_ACCORDION_CONTENT).fadeOut(short).promise().always(() => {
                element.addClass(CLASS_CLOSED);
              });
            } else {
              element.find(SELECTOR_ACCORDION_CONTENT).slideUp(short).promise().always(() => {
                element.addClass(CLASS_CLOSED);
              });
            }
          }
        });

        // On desktop colour slides will fade instead of slide
        if(responsiveMediaAbove('SMALL') && slide.hasClass('is-color')) {
          content.fadeIn(short).promise().always(() => {
            slide.removeClass(CLASS_CLOSED);
          });
        } else {
          content.slideDown(short).promise().always(() => {
            slide.removeClass(CLASS_CLOSED);
          });
        }
      } else {
        // Otherwise just close the open slide
        // On desktop colour slides will fade instead of slide
        if(responsiveMediaAbove('SMALL') && slide.hasClass('is-color')) {
          content.fadeOut(short).promise().always(() => {
            slide.addClass(CLASS_CLOSED);
          });
        } else {
          content.slideUp(short).promise().always(() => {
            slide.addClass(CLASS_CLOSED);
          });
        }
      }
    });
  }

  contentBlockIsOnPage(contentBlock, perPage, currentPage, productsShown) {
    // OG Position of the block from the data attr
    const blockOriginalPosition = parseInt(contentBlock.attr('data-content-block-position')) || 999;

    // Index of the block in the printed blocks array
    const blockIndex = this.printedContentBlocks.indexOf(blockOriginalPosition);

    // How many content blocks have been printed and what is their size?
    const offset = this.getContentBlockSize();
    const countOfPrintedBlocks = this.printedContentBlocks.length * offset;
    let blockNewPosition = blockOriginalPosition + countOfPrintedBlocks;

    if(blockIndex != -1) blockNewPosition -= offset;

    // Hide blocks with index past number of products
    if(blockOriginalPosition > productsShown) return false;

    const shouldBlockBeOnPage = blockNewPosition < (currentPage * perPage);

    if(shouldBlockBeOnPage && blockIndex == -1) {
      this.printedContentBlocks.push(blockOriginalPosition);
    } else {
      this.printedContentBlocks.splice(blockIndex, 1);
    }

    return shouldBlockBeOnPage;
  }

  getContentBlockPosition(cb, thumbnails) {
    if (thumbnails.length <= 0) return -1; // No thumbnails? hide

    if(this.contentBlockIsOnPage(cb, this.pagination.template.perPage, this.pagination.getCurrentPage(), thumbnails.length)) {
      cb.addClass(CLASS_VISIBLE);
    } else {
      cb.removeClass(CLASS_VISIBLE);
      return;
    }

    const originalPosition = parseInt(cb.attr(ATTR_CONTENT_BLOCK_POSITION));
    return originalPosition;
  }

  getContentBlocksForPage(params) {
    const productsShown = params.paginator.template.draw.drawnProducts.length;

    try {
      const contentBlocks = params.contentBlocks.filter(c => {
        return this.contentBlockIsOnPage($(c), params.perPage, params.page, productsShown);
      });

      if (contentBlocks.length) return contentBlocks;

    } catch(e) {
      console.error(e);
      return -1;
    }
  }

  getContentBlockSize() {
    return 4;
  }

  generatePrint(v) {
    const p = v.product;
    const p2 = { ...p }; // Duplicate
    p2.variants = p2.variants.map(variant => {
      const v2 = { ...variant }; // Duplicate
      delete v2.product; // Remove circular reference
      return v2;
    });
    return productThumbnailPrint(p2, 'c-collection');
  }

  onClearFilterClick(e) {
    e.preventDefault();
    const self = $(e.currentTarget);
    const navGroup = self.closest(SELECTOR_FACETED_NAV_GROUP);
    navGroup.find('input[type="checkbox"]').prop('checked', false);
    const navPrefix = navGroup.attr(ATTR_FACETED_NAV_TAG_PREFIX);
    const navFilter = this.filters.filters.find(f => f.handle === navPrefix);
    navFilter.setSettings(null);
  }

  onVariantsDrawn(variants) {
    if(variants && variants.length) {
      this.count.removeClass(CLASS_HIDDEN);
      this.noResults.removeClass(CLASS_VISIBLE);
      initialiseThumbnails();
    } else {
      this.count.addClass(CLASS_HIDDEN);
      this.noResults.addClass(CLASS_VISIBLE);
    }

    const unpaginatedProductCount = this.draw.unpaginatedProductCount;
    if(unpaginatedProductCount != '' && unpaginatedProductCount != undefined) {
      // display number of products visible in the page count
      const message = t('collections.collection.product_count', {
        count: variants.length,
        total: unpaginatedProductCount
      });

      this.count.html(message);
    }
  }

  getContentBlockPosition(cb, thumbnails) {
    if(thumbnails.length <= 0) return -1; //No thumbs? hide

    const pos = parseInt(cb.attr(ATTR_CONTENT_BLOCK_POSITION));

    if(isNaN(pos) || !isFinite(pos)) return 9999;

    if(pos > thumbnails.length - 1) {
      cb.removeClass(CLASS_VISIBLE);
    } else {
      cb.addClass(CLASS_VISIBLE);
    }

    return pos;
  }

  onReadMoreToggle(e) {
    const button = $(e.target);
    const container = button.parents(SELECTOR_READ_MORE_CONTAINER);
    container.toggleClass(CLASS_OPEN);
  }

  onFilterDrawerToggle(e) {
    e.preventDefault();

    const filtersContainer = this.container.find(SELECTOR_FILTERS);

    if(filtersContainer.hasClass(CLASS_OPEN)) {
      filtersContainer.removeClass(CLASS_OPEN);
      documentEnableScroll();
    } else {
      documentDisableScroll();

      /**
       * Just incase the promobar text goes over multiple lines - this is likely
       * especially on extra small screens below 375px.
       */
      const html = $('html');
      const promobarHeight = $('[data-promobar]').outerHeight();
      const newHeight = window.innerHeight - promobarHeight;
      filtersContainer.css({
        'height':  `${newHeight}px`,
        'top': `${promobarHeight}px`
      });

      // Scroll to the top of the page first to line up with the promobar
      if(html.scrollTop() > 0) {
        html.animate({ scrollTop: 0 }, short, 'linear', () => {
          filtersContainer.addClass(CLASS_OPEN);
        });
      } else {
        filtersContainer.addClass(CLASS_OPEN);
      }
    }
  }

  onClickOutsideFacet(e) {
    // only proceed if actually outside of facet
    if(e.target.closest(SELECTOR_ACCORDION_SLIDE)) return;
    // close all facets
    this.container.find(SELECTOR_ACCORDION_SLIDE).each((i, e) => {
      const element = $(e);

      if(!element.hasClass(CLASS_CLOSED) && responsiveMediaAbove('SMALL')) {
        // On desktop colour slides will fade instead of slide
        if(element.hasClass('is-color')) {
          element.find(SELECTOR_ACCORDION_CONTENT).fadeOut(short).promise().always(() => {
            element.addClass(CLASS_CLOSED);
          });
        } else {
          element.find(SELECTOR_ACCORDION_CONTENT).slideUp(short).promise().always(() => {
            element.addClass(CLASS_CLOSED);
          });
        }
      }
    });
  }
}

$(() => {
  const container = $(SELECTOR_COLLECTION_TEMPLATE);
  if(!container || !container.length) return;
  if(container.attr('data-initialized')) return;

  // Create the template, we use window.collection for TESTING only!!
  window.collection = new CollectionTemplate(container);
});
