/*
 *  Collection Page Numbers
 *    Extends the ajax collection page numbers to add some additional functionality
 *
 *  Version:
 *    1.0.0 - 2021/01/12
 */

import { LoadMore } from '@process-creative/slate-ajax-collection';
import { CLASS_HIDDEN } from '../../../tools/constants/classes';

export class CustomLoadMore extends LoadMore {
  getOffsets(params) {
    params = { paginator: this, ...params };
    
    return {
      start: this.pagination.getStartOffset(params),
      end: this.getEndOffset(params)
    };
  }

  updateVisibility() {
    let currentPage = ((this.variants || []).length + (this.contentBlockCount || 0) >> 0) / this.pagination.getPerPage();

    if (!currentPage || currentPage === 0) currentPage = this.pagination.getTotalPages();

    if(this.pagination.getCurrentPage() >= Math.ceil(currentPage)) {
      return this.container.addClass(CLASS_HIDDEN);
    }

    return this.container.removeClass(CLASS_HIDDEN);
  }


  getEndOffset(params) {
    // Start with the start offset of THIS page
    let offset = 0;
    this.contentBlockCount = this.pagination.getOffsetDueToContentBlocks(params);

    // Offset this pages' content blocks
    if(this.pagination.paginateContentAware && this.template.content) {
      offset -= this.pagination.getOffsetDueToContentBlocks(params);
    }

    return offset;
  }

  paginate(variants) {
    this.variants = variants;

    const params = {
      perPage: this.pagination.getPerPage(),
      page: this.pagination.getCurrentPage(),
      paginator: this,
      variants
    }

    // Start & End Index in array
    params.start = 0;
    params.end = this.pagination.paginator.getEnd(params);

    // Get Offsets
    params.offsets = this.getOffsets(params);
    params.end += params.offsets.end;

    // Paginated array
    return variants.slice(params.start, Math.min(params.variants.length, params.end));
  }
}
