import { Product } from "../../tools/product/product";
import { t } from "../../tools/utilities/string-tools";
import { printMoney, pictureGenerate, escapeString, getCountOfVariantInCart } from '@process-creative/slate-theme-tools';

const STR_ADD = t('products.product.add_to_cart');
const STR_ADDED = t('products.product.added_to_cart');
const STR_SOLD_OUT = t('products.product.sold_out');
const STR_FREE = t('products.product.free');
const STR_VIEW = t('products.product.view_product');

/**
 *
 * @param product - Product JSON
 * @returns string of product thumbnail html
 */
export const productThumbnailPrint = (product: Product, clazz?: string) => {
  const variant = product.selected_or_first_available_variant || product.variants[0];
  const { tags } = product;
  let hasHover = false;
  if(product.media.length > 1) hasHover = true;
  let colourClass = '';
  /**
   * Loop through product tags looking for anything beginning with 'thumbnail-color_'
   * If tag exists, it will add a class to the thumbnail
   * which will be used for the thumbnail button color
   * Otherwise it will use default thumbnail class
   */
  const tagProperty = 'thumbnail-colour_';
  for(let i = 0; i < tags.length; i++) {
    const tag = tags[i].toLowerCase();
    if(tag.includes(tagProperty)){
      colourClass = 'is-colour-' + tag.split(tagProperty)[1];
      break;
    }
  }

  // Pricing
  const pricingCreate = () => {
    if(!variant) return '';

    // these are equivalent to the params passed to object.product-pricing.liquid
    let paramClass = "o-product-thumbnail__pricing";
    let paramPrice = variant.price;
    let paramComparePrice = variant.compare_at_price;

    // this part is equivalent to object.product-pricing.liquid
    let paramComparePriceMoney = ``;
    let comparePriceHtml = ``;
    let hasCompare = false;
    if(paramComparePrice) {
      paramComparePriceMoney = printMoney(paramComparePrice);

      let compareClasses = `o-product-pricing__money o-product-pricing__compare ${paramClass}-money ${paramClass}-compare`;

      comparePriceHtml = `
        <span
          class="${compareClasses}"
          data-currency
          data-money="${paramComparePrice}"
          data-product-compare-money
        >
          ${paramComparePriceMoney}
        </span>
      `

      if(paramComparePrice && paramComparePrice > paramPrice) {
        hasCompare = true;
      }
    }

    let hasCompareResult = '';
    if(hasCompare) {
      hasCompareResult = 'has-compare'
    }

    let priceResult = ``;
    if(paramPrice == 0) {
      priceResult = `
        <span
          class="o-product-pricing__money o-product-pricing__price ${paramClass}-money ${paramClass}-price"
          data-currency
          data-product-money
        >
          ${STR_FREE}
        </span>
      `;
    } else {
      priceResult = `
        <span
          class="o-product-pricing__money o-product-pricing__price ${paramClass}-money
          ${paramClass}-price ${hasCompareResult}"
          data-currency
          data-money="${paramPrice}"
          data-product-money
        >
          ${printMoney(paramPrice)}
        </span>
      `;
    }

    let objectProductPricing = `
      <p
        class="o-product-pricing o-paragraph-2 ${paramClass} ${hasCompareResult}"
        data-product-pricing
      >
        ${comparePriceHtml}
        ${priceResult}
      </p>
    `;

    return `
      <a href="${product.url}" class="o-product-thumbnail__pricing-container">
        ${objectProductPricing}
      </a>
    `;
  }

  // Badge
  let badge:string|null = null;
  product.tags.some(t => {
    const tSplit = t.split('Badge_');
    if(tSplit.length !== 2) return false;
    badge = tSplit[1];
    return true;
  })

  // Image
  const imageCreate = () => {
    const imageSrc = product.featured_image || product.image || '';
    let hoverMedia;
    let hasHover = false;
    let media = product.media;
    let hoverHTML = '';
    if (media.length > 1) {
      hoverMedia = media[1];
      hasHover = true;
    } 

    if(hoverMedia && hoverMedia.media_type === 'image'){
     hoverHTML = pictureGenerate({
        class: `o-product-thumbnail__hover-image o-responsive-image`,
        src: hoverMedia.image_src,
        srcSize: 410,
        sizes: [
          { size: 280, screen: 375 },
          { size: 350, screen: 500 }
        ]
      });
    } else if (hoverMedia && hoverMedia.media_type == 'video'){
        hoverHTML = `
        <div class="o-html5-video o-product-thumbnail__hover-image o-responsive-image" data-video-container>
          <div class="o-html5-video__video-container">
            <video class="o-html5-video__video" muted="" autoplay="" loop="" playsinline="" preload="metadata" data-video=""><source src="${hoverMedia.video_src}" type="video/mp4"><p>
                Your browser doesn't support HTML5 video.
                Here is a link to the video instead:${hoverMedia.video_src}
              </p>
            </video>
          </div>
        </div>
        `
    }

    return `
      <a href="${product.url}" class="o-product-thumbnail__image-container o-responsive-image-container">
        ${pictureGenerate({
          class: `o-product-thumbnail__image o-responsive-image`,
          src: imageSrc,
          srcSize: 410,
          sizes: [
            { size: 280, screen: 375 },
            { size: 350, screen: 500 }
          ]
        })}
        ${hoverHTML}
      </a>
    `;
  }

  // ATC button
  const atcButtonCreate = (clazz: string) => {
    const isInCart = getCountOfVariantInCart(variant.id) > 0;
    let buttonLabel;

    if (isInCart){
      buttonLabel = STR_ADDED;
    } else if (variant.available) {
      buttonLabel = STR_ADD;
    } else {
      buttonLabel = STR_SOLD_OUT;
    }

    const svgShape = clazz.includes('is-mobile') ? `
      <svg
        class="o-product-thumbnail__button-shape o-btn__shape is-mobile"
        fill="#E4987F" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 110 48"
      >
        <path
          d="M95.863 1.215c-25.508-3.178-75.979.29-83.28 5.643-7.301 5.353-21.916 23.81-4.064 28.918 17.852 5.107 73.748 16.727 85.415 8.752 11.668-7.974 27.438-40.135 1.93-43.313Z">
        </path>
      </svg>
    ` : `
      <svg
        class="o-product-thumbnail__button-shape o-btn__shape is-desktop"
        fill="#E4987F" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 99 84"
      >
        <path
          d="M75.901.494C51.697 1.76 8.408 20.338 4.434 31.015.459 41.693-3.844 75.818 14.454 79.681c18.3 3.862 73.534 8.754 80.159-7.355C101.236 56.218 100.105-.773 75.9.495Z">
        </path>
      </svg>
    `;

    // If product has only 1 product we'll show the atc button
    if(product.has_only_default_variant || product.variants.length <= 1) {
      return `
        <div class="o-product-thumbnail__button-wrapper ${clazz}">
          <button
            type="submit"
            title="${product.title}"
            class="o-product-thumbnail__button o-btn is-secondary has-js ${colourClass}
            ${!variant.available && 'is-disabled'}
            ${isInCart && 'is-added'}"
            data-product-thumbnail-add-btn
            ${!variant.available ? 'disabled="disabled"' : ''}
          >
            ${svgShape}

            <span
              class="o-product-thumbnail__button-label o-btn__label"
              data-product-thumbnail-add-btn-label
            >
              ${buttonLabel}
            </span>
          </button>
        </div>
      `;
    }

    // If product has more then 1 variant we'll just link to the PDP
    return `
      <div class="o-product-thumbnail__button-wrapper ${clazz}">
        <a
          href="${product.url}"
          title="${product.title}"
          class="o-product-thumbnail__button o-btn is-secondary"
        >
          ${svgShape}
          <span
            class="o-product-thumbnail__button-label o-btn__label"
          >
            ${STR_VIEW}
          </span>
        </a>
      </div>
    `;
  }

  return `
    <div
      class="o-product-thumbnail ${clazz}__thumbnail ${hasHover ? ' has-hover-image' : ''}"
      data-product-thumbnail
      data-variant-id="${variant.id}"
    >
      <script type="application/json" data-product-json>
        ${JSON.stringify(product)}
      </script>

      <div class="o-product-thumbnail__inner">

        <div class="o-product-thumbnail__image-wrapper ${clazz}__image-wrapper">
          ${imageCreate()}

          ${atcButtonCreate('is-desktop')}
        </div>

        <a href="${product.url}" class="o-product-thumbnail__title-container">
          <h3 class="o-product-thumbnail__title ${clazz}__title o-heading--6">
            ${product.title}
          </h3>
        </a>

        ${badge === null ? '' : `
          <span class="o-product-thumbnail__badge o-paragraph--1">
            ${escapeString(badge)}
          </span>
        `}

        ${pricingCreate()}
      </div>

      ${atcButtonCreate('is-mobile')}
    </div>
  `;
}
