/*
 *  Custom Availability Filter
 *    - Extends the ajax collection filter class to add some additional
 *    functionality.
 *    - Gives three options: all products, only available products and only
 *    sold out products.
 *
 *  Version:
 *    1.0.0 - 2021/11/23
 */

import { Filter } from '@process-creative/slate-ajax-collection';
import { handlize } from '../../../tools/utilities/string-tools';

export class CustomAvailabilityFilter extends Filter {
  constructor(template, handle, available) {
    super(template, handle);
    this.available = available || 'all';
  }

  getSettings() {
    return this.available;
  }

  getSetting(value) {
    const setting = handlize(value) == handlize(this.available);
    if(!setting) return false;
    return setting;
  }

  setSetting(key, value) {
    return this.setAvailability(value);
  }

  setSettings(value) {
    return this.setAvailability(value);
  }

  setAvailability(value) {
    if(Array.isArray(value)) value = value[0];
    if(this.getSetting(value) === value) return;
    if(value === null) value = false;
    this.available = value;
    this.filters.onFilterUpdate();
  }

  filter(p, v) {
    if(!this.available || this.available == 'all') return true;
    if(this.available == 'available') return v.available;
    if(this.available == 'sold-out') return !v.available;
  }
}
