/*
 *  Custom Sort
 *    Extends the ajax collection sorter to add some additional functionality
 *
 *  Version:
 *    1.0.0 - 2021/11/22
 */

import { Sorter } from '@process-creative/slate-ajax-collection';
import * as SortMethods from '@process-creative/slate-ajax-collection/dist/sort/methods/index';
import { t } from '../../../tools/utilities/string-tools';

const SELECTOR_SORT_INNER = '[data-sort-custom-inner]';
const SELECTOR_SORT_VALUE_NAME = '[data-sort-custom-header]';
const ATTR_SORT_VALUE = 'data-collection-template-sort-value';

export class CustomSort extends Sorter {
  constructor(template, sortElement) {
    super(template);
    this.sortElement = sortElement;
    this.sortInner = sortElement.find(SELECTOR_SORT_INNER);
    this.sortValue = sortElement.find(SELECTOR_SORT_VALUE_NAME);

    sortElement.on('click touchend', `[${ATTR_SORT_VALUE}]`, e => this.onChange(e));
  }

  onChange(e) {
    let element = $(e.currentTarget);
    this.template.sort.setSort(element.attr(ATTR_SORT_VALUE));
  }

  onUpdate(selected) {
    const x = Object.entries(SortMethods).map(([key,value]) => {
      //Skip invalid elements
      if(!value || !value.isVisible) return '';
      if(!value.isVisible(this.template)) return '';

      //Is checked?
      let checked = selected === value;

      //Generate print
      return `
        <li
          class="c-collection-faceted-nav__check-item o-paragraph--2"
          ${ATTR_SORT_VALUE}="${value.handle}"
        >
          <span class="c-collection-faceted-nav__check-item-inner ${checked?'is-selected':''}">
            ${this.getOptionName(value, checked)}
          </span>
        </li>
      `;
    }).join('');
    this.sortInner.html(x);
    this.sortValue.html(`
      <span class="c-collection-faceted-nav__title-prefix">
        ${t('collections.collection.sort_label')}
        <span class="c-collection-faceted-nav__title-seperator">:</span>
      </span>
      <span class="c-collection-faceted-nav__title-value">
        ${selected.name}
      </span>
    `);
  }
}
